const DEFAULT_VERSION = 0;

interface IStorageOptions<T> {
  isSessionOnly?: boolean;
  noCache?: boolean;
  version?: number;
  default?: T;
}

export class ClientStorage<T = any> {
  readonly version: number;
  private cache: T | null = null;
  private isCached = false;
  private readonly default: T | undefined;
  private readonly storage: Storage;
  private readonly useCache: boolean;
  lastUpdated = new Date(0);

  constructor(
    private readonly key: string,
    {
      isSessionOnly = false,
      noCache = false,
      version = DEFAULT_VERSION,
      default: def,
    }: IStorageOptions<T> = {},
  ) {
    this.version = version;
    this.useCache = !noCache;
    this.storage = isSessionOnly ? sessionStorage : localStorage;
    this.default = def;
  }

  get() {
    if (!this.useCache) {
      return this.load();
    }

    if (this.isCached) {
      return deepClone(this.cache);
    }

    this.isCached = true;
    this.cache = this.load();
    return this.cache;
  }

  set(json: T) {
    this.save(json);
  }

  save(json: T) {
    const data = {
      data: json,
      version: this.version,
      updated: Date.now(),
    };

    if (this.useCache) {
      this.cache = json;
      this.isCached = true;
    }

    this.lastUpdated = new Date();
    this.storage.setItem(this.key, JSON.stringify(data));
  }

  load(): T | null {
    const json = this.storage.getItem(this.key);

    if (!json) {
      return this.getNoValue();
    }

    const content = JSON.parse(json);

    if (content.version !== this.version) {
      this.reset();
      return this.getNoValue();
    }

    if (this.useCache) {
      this.cache = deepClone(content.data);
      this.isCached = true;
    }

    this.lastUpdated = content.updated;
    return content.data;
  }

  loadAsync(delay = 100) {
    return new Promise(resolve => {
      setTimeout(() => resolve(this.load()), delay);
    });
  }

  clear() {
    const value = this.get();
    this.reset();
    return value;
  }

  reset() {
    this.storage.removeItem(this.key);

    this.cache = null;
    this.isCached = true;
  }

  private getNoValue() {
    return this.default == null ? null : this.default;
  }
}

function deepClone<T>(target: T): T {
  return typeof target === 'object'
    ? JSON.parse(JSON.stringify(target))
    : target;
}
